"use client"

import { useState, useMemo } from "react"
import CategoryForm from "@/components/category-form"
import AuditItemForm from "@/components/audit-item-form"
import AuditTable from "@/components/audit-table"
import DateFilter from "@/components/date-filter"
import ExcelJS from "exceljs"
import type { Category, AuditItem, DateFilter as DateFilterType } from "@/lib/types"

interface HousekeepingAuditProps {
  categories: Category[]
  setCategories: (categories: Category[]) => void
}

export default function HousekeepingAudit({ categories, setCategories }: HousekeepingAuditProps) {
  const [showCategoryForm, setShowCategoryForm] = useState(false)
  const [showItemForm, setShowItemForm] = useState(false)
  const [selectedCategoryId, setSelectedCategoryId] = useState<string | null>(null)
  const [editingItem, setEditingItem] = useState<AuditItem | null>(null)
  const [dateFilter, setDateFilter] = useState<DateFilterType>({})

  // Add new category
  const handleAddCategory = (name: string) => {
    const newCategory: Category = {
      id: Date.now().toString(),
      name,
      items: [],
      createdAt: new Date().toISOString(),
    }
    setCategories([...categories, newCategory])
    setSelectedCategoryId(newCategory.id)
    setShowCategoryForm(false)
    setShowItemForm(true)
  }

  // Add multiple items
  const handleAddItems = (items: Omit<AuditItem, "id" | "status" | "createdAt">[]) => {
    if (!selectedCategoryId) return

    const newItems: AuditItem[] = items.map((item, index) => ({
      ...item,
      id: (Date.now() + index).toString(),
      status: "pending",
      createdAt: new Date().toISOString(),
    }))

    setCategories(
      categories.map((cat) =>
        cat.id === selectedCategoryId
          ? { ...cat, items: [...cat.items, ...newItems] }
          : cat
      )
    )
    setShowItemForm(false)
  }

  // Edit single item
  const handleEditItem = (item: Omit<AuditItem, "id" | "status" | "createdAt">) => {
    if (!editingItem || !selectedCategoryId) return

    setCategories(
      categories.map((cat) =>
        cat.id === selectedCategoryId
          ? {
              ...cat,
              items: cat.items.map((i) =>
                i.id === editingItem.id ? { ...i, ...item, status: i.status } : i
              ),
            }
          : cat
      )
    )
    setEditingItem(null)
    setShowItemForm(false)
  }

  // Toggle status
  const handleToggleStatus = (categoryId: string, itemId: string, status: "checked" | "crossed") => {
    setCategories(
      categories.map((cat) =>
        cat.id === categoryId
          ? {
              ...cat,
              items: cat.items.map((item) =>
                item.id === itemId
                  ? { ...item, status: item.status === status ? "pending" : status }
                  : item
              ),
            }
          : cat
      )
    )
  }

  // Updated Export Function to Match PDF/Image Exactly
  const handleExport = async () => {
    const workbook = new ExcelJS.Workbook()
    const sheet = workbook.addWorksheet("Housekeeping Audit", {
      pageSetup: { paperSize: 9, orientation: "landscape", fitToPage: true, fitToWidth: 1, fitToHeight: 0 },
      properties: { defaultRowHeight: 20, defaultColWidth: 15 },
    })

    // Fetch logo
    let logoId;
    try {
      const logoResponse = await fetch(
        "https://res.cloudinary.com/protenders/image/upload/s--HAKnWaBu--/c_limit,d_default_logo,dpr_auto,f_auto,fl_progressive:semi,q_auto:eco,w_auto:100/244ed00a56bf535f459230ec60059cb9.jpg"
      );
      const logoBuffer = await logoResponse.arrayBuffer();
      logoId = workbook.addImage({
        buffer: logoBuffer,
        extension: "jpeg",
      });
      sheet.addImage(logoId, {
        tl: { col: 0.1, row: 0.1 },
        ext: { width: 120, height: 60 },
      });
    } catch (e) {
      console.error("Failed to load logo:", e);
    }

    // Title
    sheet.mergeCells("A2:G2");
    const titleCell = sheet.getCell("A2");
    titleCell.value = "HOUSEKEEPING AUDIT CHECKLIST";
    titleCell.font = { bold: true, size: 16, color: { argb: "FF000080" } }; // Navy blue
    titleCell.alignment = { horizontal: "center", vertical: "middle" };

    // Form Fields
    sheet.getCell("A4").value = "Base Location :";
    sheet.mergeCells("B4:D4");
    sheet.getCell("B4").value = "______________________________";
    sheet.getCell("E4").value = "Inspected by:";
    sheet.mergeCells("F4:G4");
    sheet.getCell("F4").value = "__________________";

    sheet.getCell("A5").value = "Ward/Unit/Department:";
    sheet.mergeCells("B5:D5");
    sheet.getCell("B5").value = "______________________________";
    sheet.getCell("E5").value = "Date:";
    sheet.mergeCells("F5:G5");
    sheet.getCell("F5").value = new Date().toLocaleDateString();

    sheet.getCell("E6").value = "Time:";
    sheet.mergeCells("F6:G6");
    sheet.getCell("F6").value = new Date().toLocaleTimeString().slice(0, 5);

    // Measurable Standard and Key
    sheet.mergeCells("A7:D10");
    const standardCell = sheet.getCell("A7");
    standardCell.value = "Measurable Standard: Clean is defined as the area is free of dust, stains, rubbish and wetness, looks tidy, neat, orderly and is spotless.\nKEY: (‚àö) Met the Standard\n(X) Didn't Meet the Standard\n(N/A) Not Applicable";
    standardCell.font = { size: 10 };
    standardCell.alignment = { wrapText: true, vertical: "top", horizontal: "left" };

    // Action Timeframe Legend
    sheet.mergeCells("E7:G10");
    const timeframeCell = sheet.getCell("E7");
    timeframeCell.value = "Action Timeframe:\nImmediate (within 24 hrs.)\nUrgent (5 days)\nOngoing (1 month)\nOpen (un specified time due to financial, administrative or engineering constraints)";
    timeframeCell.font = { size: 10 };
    timeframeCell.alignment = { wrapText: true, vertical: "top", horizontal: "left" };

    let currentRow = 12;

    // Define columns
    sheet.columns = [
      { header: "#", key: "no", width: 5 },
      { header: "Elements", key: "element", width: 50 },
      { header: "Comments", key: "comments", width: 30 },
      { header: "Out of", key: "outof", width: 10 },
      { header: "Score", key: "score", width: 10 },
      { header: "Action timeframe", key: "timeframe", width: 18 },
      { header: "Action Taken Y/N", key: "actionTaken", width: 15 },
    ];

    // Table Header
    const headerRow = sheet.getRow(currentRow);
    headerRow.values = ["#", "Elements", "Comments", "Out of", "Score", "Action timeframe", "Action Taken Y/N"];
    headerRow.eachCell((cell) => {
      cell.fill = { type: "pattern", pattern: "solid", fgColor: { argb: "FFADD8E6" } }; // Light blue
      cell.font = { bold: true };
      cell.alignment = { vertical: "middle", horizontal: "center" };
      cell.border = {
        top: { style: "thin" },
        left: { style: "thin" },
        bottom: { style: "thin" },
        right: { style: "thin" },
      };
    });
    headerRow.height = 25;
    currentRow++;

    let catIndex = 0;
    let totalScore = 0;
    let totalOut = 0;
    let totalItems = 0;
    let totalChecked = 0;

    filteredCategories.forEach((category) => {
      if (category.items.length === 0) return;

      // Section Header Row
      const sectionRow = sheet.getRow(currentRow);
      sectionRow.getCell(1).value = ++catIndex;
      sectionRow.getCell(2).value = category.name.toUpperCase();
      sheet.mergeCells(`B${currentRow}:G${currentRow}`);
      sectionRow.getCell(1).font = { bold: true };
      sectionRow.getCell(2).font = { bold: true };
      sectionRow.getCell(2).alignment = { horizontal: "left" };
      sectionRow.eachCell((cell, colNumber) => {
        if (colNumber <= 7) {
          cell.fill = { type: "pattern", pattern: "solid", fgColor: { argb: "FFD3D3D3" } }; // Gray
          cell.border = {
            top: { style: "thin" },
            left: { style: "thin" },
            bottom: { style: "thin" },
            right: { style: "thin" },
          };
        }
      });
      sectionRow.height = 20;
      currentRow++;

      // Item Rows
      category.items.forEach((item) => {
        const itemRow = sheet.getRow(currentRow);
        itemRow.values = [
          "", // No #
          item.name || "",
          item.comments || "",
          item.outOf || "",
          item.score || "",
          item.frame || "",
          item.status === "checked" ? "Y" : item.status === "crossed" ? "N" : "",
        ];
        itemRow.eachCell((cell) => {
          cell.alignment = { wrapText: true, vertical: "middle", horizontal: "left" };
          cell.border = {
            top: { style: "thin" },
            left: { style: "thin" },
            bottom: { style: "thin" },
            right: { style: "thin" },
          };
        });
        itemRow.height = 20;
        currentRow++;

        totalItems++;
        if (item.status === "checked") totalChecked++;
        if (item.outOf) totalOut += Number(item.outOf);
        if (item.score) totalScore += Number(item.score);
      });

      currentRow++; // Space after category
    });

    // SCOURED and Percentage
    const scouredRow = sheet.getRow(currentRow);
    sheet.mergeCells(`A${currentRow}:F${currentRow}`);
    scouredRow.getCell(1).value = "SCOURED";
    scouredRow.getCell(1).font = { bold: true };
    scouredRow.getCell(1).alignment = { horizontal: "left" };
    scouredRow.getCell(7).value = totalOut > 0 ? `${Math.round((totalScore / totalOut) * 100)}%` : "";
    scouredRow.getCell(7).alignment = { horizontal: "center" };
    scouredRow.eachCell((cell, colNumber) => {
      if (colNumber <= 7) {
        cell.fill = { type: "pattern", pattern: "solid", fgColor: { argb: "FFADD8E6" } };
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          bottom: { style: "thin" },
          right: { style: "thin" },
        };
      }
    });
    currentRow += 2;

    // Observations Table Header
    const obsHeaderRow = sheet.getRow(currentRow);
    obsHeaderRow.values = [
      "S. No",
      "Observations",
      "Person Responsible",
      "Action Plan",
      "Time Frame",
      "Signature of Supervisor",
      "Support Service Follow up",
    ];
    obsHeaderRow.eachCell((cell) => {
      cell.font = { bold: true };
      cell.alignment = { horizontal: "center", vertical: "middle" };
      cell.fill = { type: "pattern", pattern: "solid", fgColor: { argb: "FFADD8E6" } };
      cell.border = {
        top: { style: "thin" },
        left: { style: "thin" },
        bottom: { style: "thin" },
        right: { style: "thin" },
      };
    });
    currentRow++;

    // Add 5 blank rows for observations
    for (let i = 0; i < 5; i++) {
      const blankRow = sheet.getRow(currentRow);
      blankRow.eachCell((cell, colNumber) => {
        if (colNumber <= 7) {
          cell.border = {
            top: { style: "thin" },
            left: { style: "thin" },
            bottom: { style: "thin" },
            right: { style: "thin" },
          };
        }
      });
      blankRow.height = 20;
      currentRow++;
    }

    currentRow += 2;

    // Sign & Date
    sheet.mergeCells(`A${currentRow}:C${currentRow}`);
    sheet.getCell(`A${currentRow}`).value = "SIGN & DATE_______________________";
    sheet.mergeCells(`D${currentRow}:G${currentRow}`);
    sheet.getCell(`D${currentRow}`).value = "DESIGNATION ____________________";
    currentRow += 2;

    // Footer Code
    sheet.getCell(`A${currentRow}`).value = "HAC/08/24-002";
    sheet.getCell(`A${currentRow + 1}`).value = "Housekeeping Department";

    // Download
    try {
      const buffer = await workbook.xlsx.writeBuffer()
      const blob = new Blob([buffer], {
        type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      })
      const url = URL.createObjectURL(blob)
      const a = document.createElement("a")
      a.href = url
      a.download = `Housekeeping_Audit_Checklist_${new Date().toISOString().slice(0, 10)}.xlsx`
      a.click()
      URL.revokeObjectURL(url)
    } catch (error) {
      console.error("Export failed:", error)
      alert("Failed to export Excel file. Please try again.")
    }
  }

  const openItemForm = (categoryId: string) => {
    setSelectedCategoryId(categoryId)
    setEditingItem(null)
    setShowItemForm(true)
  }

  const openEditForm = (categoryId: string, item: AuditItem) => {
    setSelectedCategoryId(categoryId)
    setEditingItem(item)
    setShowItemForm(true)
  }

  // Filter items by date
  const filteredCategories = useMemo(() => {
    if (!dateFilter.day && !dateFilter.month && !dateFilter.year) {
      return categories
    }

    return categories
      .map((cat) => {
        const filteredItems = cat.items.filter((item) => {
          const d = new Date(item.createdAt)
          return (
            (!dateFilter.day || d.getDate() === dateFilter.day) &&
            (!dateFilter.month || d.getMonth() + 1 === dateFilter.month) &&
            (!dateFilter.year || d.getFullYear() === dateFilter.year)
          )
        })
        return { ...cat, items: filteredItems }
      })
      .filter((cat) => cat.items.length > 0)
  }, [categories, dateFilter])

  const stats = useMemo(() => {
    const all = filteredCategories.flatMap((c) => c.items)
    return {
      total: all.length,
      checked: all.filter((i) => i.status === "checked").length,
    }
  }, [filteredCategories])

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-[#2E2E2E]">Housekeeping Audit List</h2>
          {categories.length > 0 && (
            <p className="text-sm text-[#2E2E2E]/60 mt-1">
              Overall: {stats.checked} / {stats.total} completed
            </p>
          )}
        </div>
        <div className="flex gap-3">
          <DateFilter onFilterChange={setDateFilter} activeFilter={dateFilter} />
          <button
            onClick={() => setShowCategoryForm(true)}
            className="px-4 py-2 text-sm font-medium text-white bg-[#17A2A2] rounded-lg hover:bg-[#17A2A2]/90"
          >
            + Add Category
          </button>
          {categories.length > 0 && (
            <button
              onClick={handleExport}
              className="px-4 py-2 text-sm font-medium text-[#2E2E2E] bg-white border border-[#D7DDE5] rounded-lg hover:bg-[#F6F7F9]"
            >
              Export to Excel
            </button>
          )}
        </div>
      </div>

      {/* Forms */}
      {showCategoryForm && (
        <CategoryForm
          onSubmit={handleAddCategory}
          onCancel={() => setShowCategoryForm(false)}
        />
      )}

      {showItemForm && selectedCategoryId && (
        <AuditItemForm
          onSubmitMultiple={handleAddItems}
          onSubmitSingle={handleEditItem}
          onCancel={() => {
            setShowItemForm(false)
            setEditingItem(null)
          }}
          initialData={editingItem}
          categoryName={
            categories.find((c) => c.id === selectedCategoryId)?.name || ""
          }
          isEditing={!!editingItem}
        />
      )}

      {/* Content */}
      {categories.length === 0 ? (
        <div className="bg-white border border-[#D7DDE5] rounded-xl p-12 text-center">
          <p className="text-[#2E2E2E]/60 mb-4">
            No categories yet. Add your first category to get started.
          </p>
          <button
            onClick={() => setShowCategoryForm(true)}
            className="px-4 py-2 text-sm font-medium text-[#2E2E2E] bg-white border border-[#D7DDE5] rounded-lg hover:bg-[#F6F7F9]"
          >
            + Add Category
          </button>
        </div>
      ) : filteredCategories.length === 0 ? (
        <div className="bg-white border border-[#D7DDE5] rounded-xl p-12 text-center">
          <p className="text-[#2E2E2E]/60 mb-4">No items match the current filter.</p>
          <button
            onClick={() => setDateFilter({})}
            className="px-4 py-2 text-sm font-medium text-[#2E2E2E] bg-white border border-[#D7DDE5] rounded-lg hover:bg-[#F6F7F9]"
          >
            Clear Filter
          </button>
        </div>
      ) : (
        <div className="space-y-6">
          {filteredCategories.map((category) => {
            const date = new Date(category.createdAt)
            const dateStr = `${date.getDate()} ${date.toLocaleString("default", { month: "short" })} ${date.getFullYear()}`

            return (
              <div
                key={category.id}
                className="bg-white border border-[#D7DDE5] rounded-xl overflow-hidden shadow-sm"
              >
                <div className="p-6 border-b border-[#D7DDE5] flex items-center justify-between bg-gradient-to-r from-[#1D3C8F]/5 to-transparent">
                  <div>
                    <h3 className="text-lg font-semibold text-[#1D3C8F]">{category.name}</h3>
                    <p className="text-sm text-[#2E2E2E]/60">Created: {dateStr}</p>
                  </div>
                  <button
                    onClick={() => openItemForm(category.id)}
                    className="px-4 py-2 text-sm font-medium text-[#17A2A2] border border-[#17A2A2] rounded-lg hover:bg-[#17A2A2]/10 transition"
                  >
                    + Add Item
                  </button>
                </div>

                <div className="p-6">
                  <AuditTable
                    items={category.items}
                    categoryId={category.id}
                    onToggleStatus={handleToggleStatus}
                    onEdit={(item) => openEditForm(category.id, item)}
                  />
                </div>
              </div>
            )
          })}
        </div>
      )}
    </div>
  )
}